import axios from 'axios'
import types from './types.js'

const state = {
    userData:{
    	username:"",
	    password:"",
	    status:""
    } 
}

const getters = {
    getUserData(state){
    	return state.userData;
    }
}

const mutations = {
    [types.GET_STATUS](state,data){
        state.userData.username = data.username;
        state.userData.password = data.password;
        state.userData.status = data.status;
    }
}

const actions = {
  getRequest({commit,state}){
      axios.get("/api/user.json").then((resp) => {
      	  console.log(resp);
      	  commit(types.GET_STATUS,resp.data.data);
      });
  }
}

export default {
	state,
	getters,
	mutations,
	actions
}


