import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/login/index.vue'
import Manage from '../components/home/manage.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/manage',
      name: 'manage',
      component: Manage
    },
    {
      path: "*",
      redirect: "/login"         //router 重定向  打开默url为/home，访问不存在的页面url为/home
    }
  ]
})



