<template>
	<div class="container-login">
		  <el-form ref="form" :model="form" label-width="80px" class="login-form">
		      <h2 class="login-title" style="center">后台管理系统</h2>
              <el-form-item style="center" label="账号" class="box">
		          <el-input class="m-l ax-icon" v-model="form.username"></el-input>
	          </el-form-item>
	          <el-form-item label="密码" class="box">
	              <el-input class="m-l" v-model="form.password" type="password"></el-input>
	          </el-form-item>

	          <el-button type="primary" @click.native="login">登录</el-button>
	          <el-button class="cancle">取消</el-button>
		  </el-form>
		  <div v-show="isBtnLoading">{{btnText}}</div>
		  <vue-canvas-nest></vue-canvas-nest>
	</div>
</template>

<script>
    import vuecanvasnest from 'vue-canvas-nest'
    import {mapGetters,mapActions} from 'vuex'

	export default {
       data() {
	      return {
	      	form:{
	      		username:'',
	      		password:''
	      	},
	      	adminInfo:{},  //登录态
	      	options: {
		        color: "rgba(225,225,225,0.5)", //Bubble color
		        radius: 15, //Bubble radius
		        densety: 0.3, // The larger the bubble density, the greater the density (suggest no more than 1).
		        clearOffset: 0.2 // The larger the bubble disappears [0-1], the longer it disappears.
		    },
		    userData:{},
	        isBtnLoading:false
	      }
	    },
	    created(){ 
	       this.$store.dispatch("getRequest");
           if(JSON.parse(localStorage.getItem("user")) && JSON.parse(localStorage.getItem("user")).username){
           	  //成功登录后，并有localStorage
           	  console.log("localStorage yes");	  
           	  this.adminInfo = {
           	  	  status:1
           	  };
           }
	    },
	    mounted(){
              if(this.userData){
              	  this.userData = this.$store.getters.getUserData;
              }
	    },
	    computed:{
           btnText(){
                if(this.isBtnLoading){
                	this.$message({ 
                		type: 'success',
                		message: '登录成功'
                    });
                	this.$router.push("manage");
	    			return;
                }
           }
	    },
	    watch:{
            adminInfo(newValue){
            	if(newValue.status){
            		this.$message({
            			type: 'success',
            			message: '检测到您之前登录过，将自动登录'
            		})
            		this.$router.push("manage");
            	}
                console.log("登录态",newValue);
            }
	    },
	    components:{
	    	"vue-canvas-nest":vuecanvasnest
	    },
	    methods:{
	    	login(){
	    		if(this.userData.status){
                    if(!this.form.username){
		    			this.$message({
		    			   type: 'warning',
		    			   message: '请填写账号'
		    		    });
		    			return;
		    		}else if(this.form.username !== this.userData.username){
                        this.$message({
                        	type: 'warning',
                        	message: '账号错误'
                        });
		    			return;
		    		}
		    		if(!this.form.password){
		    			this.$message({
		    				type: 'warning',
		    				message: '请填写密码'	
		    			})
		    			return;
		    		}else if(this.form.password !== this.userData.password){
                       this.$message({
                       	    type: 'warning',
	                       	message: '密码错误'
                        })
		    			return;
		    		}else{
		    			this.isBtnLoading = true;

                        if(!localStorage.getItem("user")){
                            let obj = JSON.stringify({ 
			                	 "username": this.userData.username, 
			                	 "password": this.userData.password
			                });
			                localStorage.setItem('user',obj);
                        }			    		
		    		}
	    		}	    		
	    	}
	    }
   }
</script>

<style scoped>
   .login-container{
   	   position: absolute;
   	   width:100%;
   	   height: 100%;
   }
   .login-form{
   	   width:350px;
   	   margin:160px auto;
   	   background-color: rgb(255,255,255,0.8);
   	   padding:28px;
   	   border-radius: 20px;
   	   box-shadow: 0 0 30px 3px rgba(119,118,118,0.33);
   }
   .el-form-item__content{
   	   margin-left: 40px;
   }
   .login-title{
   	   color:#303133;
   	   text-align:center;
   }
   .cancle{
   	   margin-left: 80px;
   	}
   .box{
   	   margin-left: 0px;
   }
</style>


